        Vue.axios.post('https://tools.simolin.cn:8000/shiro_api/encode', {
          encode_data_input: _this.encode_data_input,
          AES_key: _this.AES_key,
          ysoserial_option: _this.ysoserial_option
        })
        .then(function(response){
          if (response.data.status == 'Success'){
            _this.encode_data_output = response.data.encode_data_output
            _this.$notify({
              title: '加密成功',
              type: 'success',
              message: '请查看结果',
              duration: 2000
            });
          }else if(response.data.status == 'Error'){
            _this.$notify({
              title: '加密失败',
              type: 'warning',
              message: response.data.message,
              duration: 2000
            });
          }else{
            _this.$notify({
              title: '加密失败',
              type: 'error',
              message: '请检查输入的内容',
              duration: 2000
            });
          }
        })
        .catch(function(error){
          console.log(error)
        });
      },
      start_decode () {
        this.$notify({
          title: '解密中……',
          type: 'info',
          message: '请稍等',
          duration: 2000
        });
        var _this = this;
        Vue.axios.post('https://tools.simolin.cn:8000/shiro_api/decode', {
          decode_data_input: _this.decode_data_input
        })
        .then(function(response){
          if (response.data.status == 'Success'){
            _this.decode_AES_key = response.data.AES_key
            _this.decode_data_output = response.data.decode_data_output
            _this.$notify({
              title: '解密成功',
              type: 'success',
              message: '请查看结果',
              duration: 2000
            });
          }else if(response.data.status == 'Error'){
            _this.$notify({
              title: '解密失败',
              type: 'warning',
              message: '无可解密的AES_key或输入内容有误',
              duration: 2000
            });
          }else{
            _this.$notify({
              title: '解密失败',
              type: 'error',
              message: '请检查输入的内容',
              duration: 2000
            });
          }
        })
        .catch(function(error){
          console.log(error)
        });
